import React, { Component,PropTypes } from 'react';
import MapComponent from '../../common/map/map-component';

export default class MapFinderResultListComponent extends Component {

    constructor(props) {
        super(props);
        this.state = {
            markers:this.convertMarker(this.props.markers)
        };

    }


    componentWillReceiveProps(nextProps){



        if(this.props.markers !==   nextProps.markers  ){
            this.setState({
                markers:this.convertMarker(nextProps.markers)
            });
        }
    }


    convertMarker(arrObject){
        let arrMarker = [];
        if(arrObject) {
            for (let i = 0; i < arrObject.length; i++) {
                let temp = arrObject[i];
                let info = "";
                let comma = ' ';
                if (temp.street) {
                    info = info + temp.street;
                    comma = ", ";
                }
                if (temp.suburb) {
                    info = info + comma + temp.suburb;
                    comma = ", ";
                }
                if (temp.state) {
                    info = info + comma + temp.state;
                }

                let getTemp = {lat: temp.lat, lng: temp.lon, title: temp.name, info: info}
                arrMarker.push(getTemp);
            }
        }
        return arrMarker;
    }

    /**
     * @desc This is callback function when click on item
     * @param {object} event - Event of element
     */
    onClick(event) {
        event.stopPropagation();
        event.preventDefault();

        this.setState({


        });
    }

    onBoundsChange(numcount, center, zoom){

    }

    onNext(){

        if(this.props.onNext) return this.props.onNext();
    }

    render() {
        let srcPin = "data:image/png;base64,";
        const onBoundsChange = (numcount, center, zoom) => this.onBoundsChange(numcount, center, zoom );
        const onNext = () => this.onNext();
        let center =  {lat: 0, lng: 0};
         if(this.state.markers){
              center = this.props.center ? this.props.center : this.state.markers.length > 0 ? {lat:  this.state.markers[0].lat ,lng :  this.state.markers[0].lng} : {lat: 0, lng: 0};
         }

        return (

                   <MapComponent
                       ref="map"
                       center={center}
                       zoom={this.props.zoom}
                       markers={this.state.markers}
                       usingInfoBox={true}
                       srcPin={srcPin}
                       onNext={onNext}
                   />


        );
    }

}


MapFinderResultListComponent.propTypes = {
    onSearchClick : PropTypes.func,
    markers: PropTypes.array,
    center: PropTypes.object,
    zoom: PropTypes.number,
    onNext:  PropTypes.func,
};

MapFinderResultListComponent.defaultProps = {

    zoom: 11,
    markers: [],


};


